header{
    margin:0 auto;
    text-align: center;
    padding-top: 180px;
}
header h1{
  margin: 0;  
  font-size: 4rem;
  color:rgb(33,37, 41)
} 
header p{
    font-weight: 700;
    color:rgb(33,37,41);
    font-size: 1.75rem;
    margin-bottom: 10px;
}
header a{
    box-shadow: 0 3px 3px 0 rgba(0,0,0,.1);
    font-weight: 700;
    background-color:#1d809f ;
    border-color: #1d809f;
    color: #fff;
    padding:1rem;
    text-decoration: none;
    border-radius:.5rem ;
    display: inline-block;
}


